


















































import { Component, Vue } from 'vue-property-decorator';
import Router from 'vue-router'
import axios from 'axios';
// @ts-ignore
import rest from './../../rest.js'
declare module 'vue/types/vue' {
  // 3. Declare augmentation for Vue
  interface Vue {
    username: string
    password: string
    setGetAuth: any
    locked: boolean
    wrong: boolean
  }
}

@Component({
  components: {
  },
  methods: {
    onSubmit () {
      this.$store.commit('cAuth', rest.setAuthorizationToken({
        username: this.username,
        password: this.password
      }))
      axios.get('students/me').then(() => {
        this.$router.push({name: 'home'})
        this.setGetAuth()
        this.$store.commit('cAuth', localStorage.getItem('token'))
      }).catch(err => {
        console.log(err)
        if (err == 'Error: Request failed with status code 406') {
          this.locked = true
          setTimeout(() => {
            this.locked = false
          }, 24*1000);
        } else {
          this.wrong = true
          setTimeout(() => {
            this.wrong = false
          }, 2000);
        }
        localStorage.removeItem('token')
      })
    },
    setGetAuth () {
      axios.get(`students/me`).then((response) => {
        this.$store.commit('updateRole', response.data.role)
        this.$store.commit('updateUserId', response.data.id)
        return response.data.role
      })
    },
    onReset () {
    },
    forgotPassword () {
      this.$router.push({name: 'forgotPassword'})
    },
  },
})
export default class Login extends Vue {
  username: string = ''
  password: string = ''
  locked: boolean = false
  wrong: boolean = false
}



